<script>

import KetThuc from '../components/KetThuc.vue'
import Modau from '../components/Modau.vue'
import {Clicks} from '../data/Click' 
import LeftCom from '../components/Lefts.vue'
import RightCom from '../components/Right.vue'
export default {
  name: 'cau-be-tich-chu',
  data: () => ({
    anh: 'https://cotich.net/uploads/news/a_845419731_cau-be-tich-chu.jpg',
    per: -1,
    next: -1,
    ht: -1,
    ht2: -1,
    arrClassScc: ["", "", "", "", "", "", "", "", "", "", ""],
    kkk: 1
  }),
  metaInfo: {
    title: 'Cậu bé Tích Chu - Cổ tích thế tục Việt Nam',
    meta: [
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Cậu bé Tích Chu' },
      { name: 'Description', content: 'Cậu bé Tích Chu vì ham chơi không chăm sóc cho bà khi bệnh nên bà đã biến thành loài chim đi tìm thức ăn. Sau khi hối hận, Tích Chu vượt mọi gian khó đã tìm được nước tiên về cho bà uống. May mắn thay bà đã sống lại và từ đó hai bà cháu hết mực yêu thương nhau.' },
      { name: 'thumbnail', content: 'https://cotich.net/uploads/news/a_845419731_cau-be-tich-chu.jpg' },

      { itemprop: 'name', content: 'Truyện cổ tích' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1, maximum-scale=1' },
      { name: 'language', content: 'Vietnamese,vn' },
      { name: 'geo.region', content: 'VN' },
      { property: 'article:tag', content: 'Truyện cổ tích, Chuyện cổ tích' },
      { name: 'robots', content: 'index,follow' },
    ]
  },
  components: {
    LeftCom,
    RightCom,
    Modau,
    KetThuc
  },
  methods: {
     denmodau(){
      Clicks.clickmodau();
    },
    denketthuc(){
      Clicks.clickketthuc();
    },
    clicks(vts) {
      this.ht = vts + 1;
      this.ht2 = vts;
      this.per = vts - 1;
      this.next = vts + 1;
      if (this.ht % 2 === 0) {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], false);
        if (this.per > 0) {
          this.arrClassScc[this.per] = this.setClass(this.arrClassScc[this.per], false);
        }
      } else {
        this.arrClassScc[this.ht2] = this.setClass(this.arrClassScc[this.ht2], true);
        if (this.next < this.arrClassScc.length) {
          this.arrClassScc[this.next] = this.setClass(this.arrClassScc[this.next], true);
        }
      }
      this.kkk++;
    },
    setClass(sClass, add) {
      var f = 'flipped';
      if (add) {
        if (sClass.indexOf(f) < 0) {
          sClass = sClass + " " + f;
        }
      } else {
        if (sClass.indexOf(" " + f) > 0) {
          sClass = sClass.replace(" " + f, "");
        } else
          if (sClass.indexOf(f) > 0) {
            sClass = sClass.replace(f, "");
          }
      }
      return sClass;
    },
    getCl(vt) {
      return this.arrClassScc[vt];

    },
    getZindex(vt) {
      if (vt % 2 === 0) {
        return "z-index: " + (4 - vt) + ";"
      } else {
        return "";
      }
    }
  },
  created() {
  }
}
</script>

